
import { defineComponent, reactive, ref } from 'vue'
export default defineComponent({
  name: "AddList",
  setup(){
    const url = ref<string>('api/posts')
    const lists = reactive({        
        projectName: "",
        type: "",      
        changerA: "",
        changerB: "",
        detail1: "",
        status1: false,
        detail2: "",
        status2: false,
        detail3: "",
        status3: false,
        detail4: "",
        status4: false,
        detail5: "",
        status5: false,
        detail6: "",
        status6: false,
        detail7: "",
        status7: false,
        detail8: "",
        status8: false,
        status: false,
        isFav: false,
    });
    const overView = reactive({
        contractDay: new Date().toLocaleString(),
        contractNo: "DEW***",
        contractDuration: 360,
        projectPlace: "**",
        totalPrice: 125000,
        constructionContent: "**",
        Lessor: "",
        investment: 100000000,
        economicPerformance: 25125851,
        hetongfenjiebiao: "",
        xiangguanfang: ""
    })
    const projectTimeline = reactive({      
      items: [
        {          
          day: "2022-03-21",
          keyponit: "",
          output: "",
          qiantoudanwei: "",
          canyurenyuan: ""
        }
      ]
    })

    const addList = async (): Promise<void> => {
      if(lists.projectName.length ===0 || lists.type.length ===0) 
      { alert("")
        return}
      try{
        await fetch(url.value + '/todolists',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...lists})
        })
        await fetch(url.value + '/overview',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...overView})
        })
        await fetch(url.value + '/projectTimeline',{
          method: "POST",
          headers :{
            "Content-Type": "application/json",
          },
          body: JSON.stringify({...projectTimeline})
        })
      } catch(err){
        console.log(err)
      }
    }
    return {
      lists,
      addList,
      overView,
      projectTimeline,
    }
  },
})
